@import '../styles/mixins.scss';

.gallery {
    display: flex;
    flex-wrap: wrap; 
    justify-content: center;
}

@supports (display: grid) {
    .gallery {
        display: grid;
        grid-gap: 16px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        justify-items: stretch;
        align-items: stretch;

        @include md {
            grid-auto-flow: dense;

            .image {
                &:nth-child(3n+4) {
                    grid-column-end: span 2;
                }

                &:nth-child(2n-1) {
                    grid-row-end: span 2;
                }
            }
        }
    }
}

.placeholderText {
    color: #bbb;
    font-size: 32px;
    text-align: center;
}