@import './styles/mixins.scss';
@import './styles/base.module.scss';

.header {
	position: relative;
	text-align: center;
	color: #fff;
	background: #2f2f2f;
	padding: 4rem 0;

	@include md {
		padding: 6rem 0;
	}
	
	
	.headerInnerWrapper {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translateX(-50%) translateY(-50%);
 	}
	
	.appTitle { 
		margin: 0;
	}

	.logo {
		fill: #e40d55;
		width: 60px;
		height: 60px;
		min-height: 60px;
	}
}

main {
	max-width: 1400px;
	margin: 0 auto;
	padding: 0 1rem;

	@include lg {
		padding: 0 4rem;
	}
}