@import './mixins.scss';

:root {
    --blue: #2c5ed2;
    --dark-blue: #3470fe;
    --gray: gray;
    --dark-gray: #636363;
    --white: #eee;
    --page: #e6e6e6;
    --black: #222;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
    margin: 0;
    padding: 0;
}

body {
    background-color: var(--page);
    color: var(--black);
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1 {
    font-size: 24px;
    line-height: 1.4;
    font-weight: 400;

    @include md {
        font-size: 40px;
        line-height: 1.3;
    }
    @include lg {
        font-size: 48px;
        line-height: 1.3;
    }
}

h2 {
    font-size: 18px;
    line-height: 1.4;
    font-weight: 400;

    @include md {
        font-size: 28px;
    }
    @include lg {
        font-size: 30px;
        line-height: 1.3;
    }
}

p {
    font-size: 16px;
    line-height: 2;
    
    @include lg {
        line-height: 1.6;
    }
}